@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left{
    flex: 1;
    overflow: hidden;

    img {
      height: 100%;

      @include mobile {
        height: 75%;
        width: 100%;
      }
    }
  }
  .right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 40px;
      padding-bottom: 90px;
    }

    .itemContainer {
      display: flex;
      align-items: center;
      
      a {
        font-size: 30px;
        text-decoration: none;
        color: $mainColor;
        padding-top: 5px;

        @include mobile {
          padding-top: 0;
        }
      }
    }

    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;

        @include mobile {
          width: 200px;
          height: 20px;
        }
      }

      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;

        @include mobile {
          width: 200px;
          height: 100px;
        }
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: rgb(0, 183, 255);
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }

      span {
        color: red;
      }
    }
  }
}